import styled from "styled-components";
import posed from "react-pose";
const TextP = posed.p({
  enter: { x: 0, opacity: 1 },
  exit: { x: 50, opacity: 0 }
});
export const App = styled.div`
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: start;
  font-size: calc(10px + 2vmin);
  background: #19273a;
  &::after {
    content: #fff;
  }
`;
const Title = posed.div({
  enter: { staggerChildren: 50 }
});

export const P = styled(Title)`
  text-align: start;
  font-size: 2.4em;
  font-family: "Cormorant Garamond", serif;
  font-style: normal;
  font-weight: bold;
  white-space: pre-line;
  overflow: hidden;
  width: auto;
  color: #fff;
  padding: 0 20px;
  margin: 25px 0;
`;
export const Text = styled(TextP)`
  font-size: 1.4em;
  font-family: "Lato", sans-serif;
  font-style: normal;
  font-weight: normal;
  white-space: pre-line;
  line-height: 164%;
  overflow: hidden;
  width: auto;
  color: #fff;
  margin: 15px 0;
  padding: 0 20px;
`;
